import React from "react";
import ReactSelect, { GroupBase, SelectComponentsConfig } from "react-select";

import Label from "../Label/Label";
import { colors, spacingMap } from "../styleguide";
import Typography from "../Typography/Typography";
import { uuid } from "../utils";

import {
	comboboxClassNamePrefix, ComboboxWrapper, createStyles, LoadingIndicator, NoResultsFound,
	validationColor } from "./Combobox.styles";
import { ComboboxProps, OptionType } from "./Combobox.types";
import {
	ClearIndicator,
	Control,
	GroupHeading,
	MultiValue,
	Option,
} from "./ReactSelectComponents";

/** @description Combobox is built on top of `react-select`
 * @see [`react-select`](https://react-select.com/home) for more information
 */
const Combobox = <
	Option extends OptionType,
	IsMulti extends boolean = false,
	Group extends GroupBase<Option> = GroupBase<Option>,
>({
		dataQA,
		placeholder,
		label,
		labelHtmlFor,
		id = uuid(),
		helperText,
		options,
		isMulti,
		comboboxIcon,
		comboboxRef,
		required,
		styles,
		validation,
		...props
	}: ComboboxProps<Option, IsMulti, Group>) => {
	const customComponents: SelectComponentsConfig<Option, IsMulti, Group> = {
		Control,
		Option,
		MultiValue,
		ClearIndicator,
		GroupHeading,
		DropdownIndicator: null,
		MultiValueRemove: () => <></>,
		LoadingMessage: () => <LoadingIndicator dataQA={`${dataQA}-combobox-loading`} size="Small" />,
		NoOptionsMessage: () => (
			<NoResultsFound
				dataQA="comobobox-no-results-found"
				component="p"
				variant="bodyCopy"
				color={`${colors.black}A8`}
				m="0.625rem 0"
			>
				No results found.
			</NoResultsFound>
		),
	};

	return (
		<ComboboxWrapper m={props.m} mb={props.mb} mt={props.mt} mr={props.mr} ml={props.ml}>
			{label && (
				<Label
					dataQA={`${dataQA}-select-label`}
					htmlFor={labelHtmlFor}
					m={`0 0 ${spacingMap.xxs} 0`}
					isRequired={required}
					isInvalid={validation === "invalid"}
				>
					{label}
				</Label>
			)}
			<ReactSelect
				styles={createStyles<Option, IsMulti, Group>(styles, validation)}
				classNamePrefix={comboboxClassNamePrefix}
				placeholder={placeholder}
				inputId={id}
				options={options}
				hideSelectedOptions={false}
				closeMenuOnSelect={!isMulti}
				isMulti={isMulti}
				comboboxIcon={comboboxIcon}
				components={customComponents}
				ref={comboboxRef}
				required={required}
				validation={validation}
				{...props}
			/>
			{helperText && (
				<Typography
					dataQA={`${dataQA}-helper-text`}
					variant="caption"
					component="p"
					color={validation ? validationColor[validation] : `${colors.black}A6`}
					m={`${spacingMap.xxs}} 0 0 0`}
				>
					{helperText}
				</Typography>
			)}
		</ComboboxWrapper>
	);
};

Combobox.displayName = "Combobox";

export default Combobox;
